  /* typography */
@font-face {
  font-family: 'Circe';
  font-weight: normal;
  src: url(https://abobykin.github.iostatic/fonts/Circe-Regular.7786e39.eot);
  src: url(https://abobykin.github.iostatic/fonts/Circe-Regular.7786e39.eot) format("eot"), url(https://abobykin.github.iostatic/fonts/Circe-Regular.e8c2bdb.woff) format("woff"), url(https://abobykin.github.iostatic/fonts/Circe-Regular.c7d081a.ttf) format("truetype"), url(https://abobykin.github.iostatic/img/Circe-Regular.02f4bde.svg) format("svg");
}
@font-face {
  font-family: 'Circe';
  font-weight: bold;
  src: url(https://abobykin.github.iostatic/fonts/Circe-Bold.6c59e12.eot);
  src: url(https://abobykin.github.iostatic/fonts/Circe-Bold.6c59e12.eot) format("eot"), url(https://abobykin.github.iostatic/fonts/Circe-Bold.977dee2.woff) format("woff"), url(https://abobykin.github.iostatic/fonts/Circe-Bold.59d60af.ttf) format("truetype"), url(https://abobykin.github.iostatic/img/Circe-Bold.2ac29d8.svg) format("svg");
}
body {
  font-family: 'Circe';
  font-weight: normal;
}
h1, h2, h3, h4 {
  font-weight: bold;
}
h1 {
  font-size: 58px;
}
  /* layout */
html, body {
  margin: 0;
  padding: 0;
  color: #000;
  background: #fff;
  min-height: 100%;
  min-height: 100vh;
  font-size: 16px;
}
a {
  opacity: 0.75;
  text-decoration: none;
  -webkit-transition: all ease-out .35s;
  transition: all ease-out .35s;
}
a:hover, a:focus, a:active {
    opacity: 1;
    text-decoration: none;
}
.section a:hover, .section a:focus, .section a:active {
  opacity: 1;
  text-decoration: none;
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 15;
  background: none;
  padding: .4rem 0;
  color: #fff;
  font-size: 0.89rem;
  -webkit-transition: color .4s;
  transition: color .4s;
}
.header .navbar {
    border-radius: 0;
    background: none;
    border: none;
    font-weight: 500;
    margin: 0;
}
.header .navbar-nav {
    margin: 0 15px;
}
.header .navbar-header {
    margin-left: 15px;
}
.header .navbar-inverse .navbar-toggle {
    background: none;
    border: none;
}
.header .navbar-toggle .icon-bar {
    border-radius: 0;
    margin-top: 5px;
    -webkit-transition: color .4s;
    transition: color .4s;
}
.header a:hover, .header a:focus, .header a:active {
    opacity: 1;
    text-decoration: none;
}
.header .navbar li > a {
    color: #000;
    opacity: .8;
    padding: .5rem .25rem;
    margin-left: 1.7rem;
    font-size: 18px;
}
.header .navbar li > a:hover, .header .navbar li > a:focus, .header .navbar li > a:active {
      opacity: 1;
      color: initial;
}
.header .navbar .btn {
    padding-right: 1rem;
    padding-left: 1rem;
}
.header .logo {
    opacity: 1;
    display: inline-block;
    padding: 15px;
}
.header .auth-links:after {
    content: "";
    clear: both;
    display: table;
}
.header .auth-links {
    width: calc(100% - 230px);
    margin-right: -4px;
    margin-bottom: 4px;
}
.header .auth-links li {
      float: right;
}
.header .navbar-toggle .icon-bar {
    color: #fff;
    -webkit-transition: background-color linear .2s;
    transition: background-color linear .2s;
}
.header .contact-links {
    font-weight: bold;
    font-size: 18px;
    text-align: right;
}
.header .contact-links a {
      color: #000;
}
.header .contact-links svg {
      margin-left: 4px;
}
.footer {
  background-color: #EDE5D9;
  padding: 43px 0;
  color: #39342D;
  font-size: 16px;
}
.footer-flex-layout {
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
}
.footer a {
    color: #39342D;
}
.footer p {
    font-size: 15px;
}
.footer ul {
    list-style-type: none;
}
.footer-address {
    margin: 12px 0 8px;
}
.footer .social-links {
    margin: 50px 0 0;
}
.footer .social-links a {
      margin-right: 14px;
}
.footer-menu a {
    display: inline-block;
    padding: 5px 0;
    line-height: 1.55rem;
}
.footer-menu .sub-item {
    padding: 0;
    font-size: 15px;
}
.footer-submit-col {
    margin-left: auto;
}
.footer-submit-form > * {
    display: block;
    width: 100%;
    margin-bottom: 10px;
}
  /* buttons / inputs */
.btn {
  border: none;
  border-radius: 3px;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1rem;
  opacity: 1;
}
.btn-primary {
    background-color: #85869A;
    color: #fff;
    opacity: .9;
    -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}
.btn-primary:hover, .btn-primary:focus, .btn-primary:active {
      opacity: 1;
      background-color: #85869A;
      color: #fff;
      -webkit-box-shadow: none;
      box-shadow: none;
}
.section-buttons {
  margin: 1.5rem 0 1rem;
}
.section-button {
  height: 50px;
  line-height: 50px;
  min-width: 260px;
  padding: 0 1.9rem;
  opacity: .7;
  font-size: 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.section-button:hover, .section-button:focus {
    opacity: 1;
}
.text-input {
  font-size: 16px;
  border: 1px solid transparent;
  background-color: #fff;
  padding: 8px;
}
.text-input:focus {
  border-color: #D8C4A7;
  outline: none;
}
.form-button {
  background-color: #D8C4A7;
  color: #fff;
  height: 40px;
  line-height: 40px;
  padding: 0;
  border: none;
}
@media screen and (max-width: 768px) {
.btn {
    font-size: 14px;
    color: #fff;
}
.section-button {
    height: auto;
    width: 210px;
    padding: 0.9rem 2.1rem;
    opacity: 1;
    line-height: 1.28rem;
    white-space: normal;
}
.footer-flex-layout {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
}
}
